	module.exports = {"data":{"post":{"html":"<p>Somewhat unvoluntarily, I had a chance to informally compare health care costs in the US and Germany. A couple of visits ago, my mother got sick here and we took her to our doctor. Cost of the office visit was $20, plus about $3 for a blood test, and the bill wasn't sent out for several weeks. Last Saturday, I got sick in Minnesota and had to visit my mother's doctor. Office visit was $60, plus $35 for a throat culture. I had to pay cash on the spot, even though the bill won't be ready for my insurance for several weeks. </p>\n<p>I also got a prescription for 10 days of antibiotics, at $10 per day. That would be comparable to here, although my doctor here would never prescribe more than 5 days of antibiotics at a time. <b>Later</b> all strep tests were negative.</p>\n<p>After buying coffee this morning and milk this afternoon, I have just one 50 DM note and 8 DM in change left.</p>","fields":{"title":"E2","date":"02 Jan 2002","url":"https://papascott.de/2002-01-02-e2/"}}},"pathContext":{"slug":"/2002-01-02-e2/","prev":{"fields":{"title":"Happy E-Day","date":"01 Jan 2002","slug":"/2002-01-01-happy-e-day/"}},"next":{"fields":{"title":"Backwards Progress","date":"03 Jan 2002","slug":"/2002-01-03-backwards-progress/"}}}}

